import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './components/Header';
import Footer from './components/Footer';
import { useEffect, useState } from 'react';
import axios from 'axios';
import Home from './components/Home';
import { Route, Routes, BrowserRouter as Router} from 'react-router-dom';
import Favorites from './components/Favorites';
import Basket from './components/basket/Basket';
import Description from './components/Description';
import Form from './components/Form';
import React from 'react';



export const AppContext = React.createContext({})


function App() {
  //  
  const [tyrs, setTyrs] = useState([])
  //  
  
  // 
  const [overlayItems, setOverlayItems] = useState([])

  useEffect(()=>{
    async function axiosData(){              
      const tyrsData = await axios.get('https://64593dbb8badff578e08a791.mockapi.io/tyrs')
      const cartData = await axios.get('https://64593dbb8badff578e08a791.mockapi.io/cart')
      setTyrs(tyrsData.data)
      setOverlayItems(cartData.data)


    }
    axiosData();
  
},[])

   const deleteItems=(id)=>{
    axios.delete(`https://64593dbb8badff578e08a791.mockapi.io/cart/${id}`)
    setOverlayItems((objDelete)=> objDelete.filter(item=>item.id !==id))
   }
   const isAdded=(myId)=>{
    return overlayItems.some((objIsAdded)=> objIsAdded.myId === myId)
   }
   

  return (
    <AppContext.Provider
    value={
      {
        tyrs,
        setTyrs,
        overlayItems,
        setOverlayItems,
        isAdded,
        }
    }>
   

    <div>


      <Router>
        <Header/>
          <Routes>
            <Route path='/favorites'
              element={
                <Favorites/>
              }
              />
              <Route path='/description'
              element={
                <Description/>
              }
              />
              <Route path='/'
              element={
                <Home
                item={tyrs}
                overlayItems={overlayItems}
                setOverlayItems={setOverlayItems}
                         
                />
              }
              />

              <Route path='/form'
              element={
                <Form/>
              }
              />

              <Route path='/cart'
              element={
                <Basket
                overlayItems={overlayItems}
                deleteItems={deleteItems}
                
                />
              }
              />
            </Routes>
               
      </Router>

      <Footer/>

      
      </div>

</AppContext.Provider>

      
     
    
  );
}

export default App;
